
<template>
  <div id="app">
    <Tolist :receive="receive" @ooooo="ooooo" />
    <List
      v-for="item in todolist"
      :key="item.id"
      :Exi="item"
      :checkTodo="checkTodo"
      :delTlist="delTlist"
      :sendExi="sendExi"    
    />
    <hr style="width: 300px" v-show="this.todolist.length" />
    <li id="p" v-show="this.todolist.length">
      <el-checkbox v-model="Exiii" @click="allCheckbox($event.target.checked)"
        >已完成{{ domeTolist }}/全部{{ this.todolist.length }}
      </el-checkbox>
      <el-button
        type="primary"
        icon="el-icon-delete"
        id="delbutton"
        @click="clearAll"
      ></el-button>
    </li>
     <div style="position: fixed; right: 0; top: 0">
      <el-switch
        v-model="value"
        active-color="#13ce66"
        inactive-color="#ff4949"
        @change="change"
      >
      </el-switch>
    </div>
  </div>
</template>
<script>
// 引入组件
import Tolist from "./components/Tolist.vue";
import List from "./components/List.vue";
export default {
  name: "App",
  components: {
    Tolist,
    List,
  },
  data() {
    return {
      todolist: [
        { id: "01", content: "学习", done: true },
        { id: "02", content: "吃饭", done: false },
        { id: "03", content: "开车", done: true },
      ],
      value: true,
    };
  },
  computed: {
    // 计算已完成
    domeTolist() {
      // 第一种方法
      // let i=0
      // this.todolist.forEach((todo)=>{
      //   if(todo.done) i++
      // })
      //   return i

      // 第二种
      // return this.todolist.reduce((pre,current)=>{
      //    return pre+(current.done ? 1 : 0)
      // },0)

      // 第二种简写
      return this.todolist.reduce(
        (pre, current) => pre + (current.done ? 1 : 0),
        0
      );
    },
    Exiii: {
      get() {
        return (
          this.domeTolist === this.todolist.length && this.todolist.length > 0
        );
      },
      set(value) {
        this.allCheckbox(value);
      },
    },
  },
  methods: {
    change(s) {
      var body = document.body;
      if (s) {
        body.className = "light";
      } else {
        body.classList = "black";

      }
    },
    // 测试传值
    // 接收子组件数据
    sendExi(x) {
      console.log("我是父组件我已收到子组件1的数据", x);
    },
    ooooo(x) {
      console.log("我是父组件我已收到子组件2的数据", x);
    },
    // 全选与全不选
    allCheckbox(done) {
      this.todolist.forEach((todo) => {
        todo.done = done;
      });
    },
    // 全删
    clearAll() {
      this.$confirm("此操作将删除所选文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.todolist = this.todolist.filter((todo) => {
            return !todo.done;
          });
          this.$message({
            type: "success",
            message: "删除成功!",
            duration: 2000,
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
            duration: 2000,
          });
        });
    },
    // 添加
    receive(x) {
      this.todolist.unshift(x);
    },
    // 删除
    delTlist(id) {
      this.todolist = this.todolist.filter((todo) => {
        return todo.id !== id;
      });
    },
    // 复选框取反
    checkTodo(id) {
      this.todolist.forEach((Exi) => {
        if (Exi.id === id) Exi.done = !Exi.done;
      });
    },
  },
};
</script>

<style >
#delbutton {
  position: absolute;
  right: 0;
}
* {
  margin: 0;
  padding: 0;
}
#app {
  width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 80px auto;
  border: 1px solid skyblue;
  border-radius: 10px;
}
#p {
  height: 50px;
}
.light {
  background-color: white;
}
.black {
  background-color: rgba(218, 175, 175,0.5);
}
body {
  transition: all 0.5s;
  background-color: white;
}
</style>

