<template>
  <div id="root">
    <input
      type="text"
      id="Input"
      placeholder="输入按下空格添加"
      @keyup.enter="add"
      v-loading.fullscreen.lock="fullscreenLoading"
    />
  </div>
</template>
<script>
export default {
  name: "Tolist",
  data() {
    return {
      fullscreenLoading: false,
    };
  },
  methods: {
    add(e) {
      const toObj = {
        id: Math.floor(Math.random() * 1000),
        content: e.target.value,
        done: false,
      };
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
        toObj.content != "" ? this.receive(toObj) +
            this.$message({
              message: "添加成功",
              type: "success",
              duration: 2000,
              showClose: true,
            })
          : this.$message({
              message: "不能为空",
              type: "error",
              duration: 2000,
              showClose: true,
            });
        document.querySelector("input").value = " ";
      }, 1000);
    },
  },
  props: ["receive",'sendExi','borrr'],
};
</script>
<style lang="css" scoped>
#root {
  width: 300px;
  /* height: 300px; */
  /* border: 1px solid; */
  display: flex;
  flex-direction: column;
  align-items: center;
}
#Input:focus{
    border: 2px solid skyblue;
}
#Input{
    margin-top: 10px;
    /* transform: translateY(-5px); */
    text-indent: 10px;
    box-sizing: border-box;
    border: 1px solid rgb(199, 196, 196);
    height: 40px;
}

</style>