<template>
  <div id="root">
    <ul>
      <li>
        <el-button
          title="删除"
          type="danger"
          icon="el-icon-close"
          id="ErrorBtn"
          @click="delTisttt(Exi.id)"
          size='mini'
        ></el-button>
        <el-checkbox
          v-model="Exi.done"
          :label="this.Exi.content"
          @click="handeCheck(Exi.id)"  
        ></el-checkbox>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "List",
  props: ["Exi", "checkTodo", "delTlist","sendExi"],
  methods: {
    // 向父组件发送数据
    sendExiiiii(){
        this.sendExi(this.delTisttt)
    },
    // 复选框取反
    handeCheck(id) {
      this.checkTodo(id);
    },
    // 删除
    delTisttt(id) {
      if (confirm("确定删除吗？")) {
        this.delTlist(id),
          this.$message({
            message: "删除成功",
            type: "success",
            duration: 1000,
            showClose: true,
          });
      }
    },
  },
};
</script>
<style lang="css" >
#Input {
  width: 300px;
  height: 30px;
  border-radius: 4px;
  border: 1px solid;
}

#Input:focus {
  outline: none;
}

li {
  /* flex: 1; */
  width: 300px;
  height: 40px;
  list-style: none;
  position: relative;
    display: flex;
    align-items: center;
}
li:hover #ErrorBtn {
  opacity: 1;
}
ul {
  /* border: 1px solid; */
  width: 300px;
  display: flex;
  flex-direction: column;


}
#ErrorBtn {
  position: absolute;
  right: 0;
  opacity: 0;
}
</style>